"use client";
import { useSession, signIn } from "next-auth/react";
import { useRouter } from "next/router";
import { useEffect } from "react";

export default function Home() {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (session) {
      router.replace("/dashboard"); // login ayithe dashboard
    }
  }, [session, router]);

  if (status === "loading") return null;

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background: "#f5f5f5",
        fontFamily: "Segoe UI",
      }}
    >
      <div
        style={{
          background: "#fff",
          padding: "40px 50px",
          borderRadius: 12,
          boxShadow: "0 10px 30px rgba(0,0,0,0.1)",
          textAlign: "center",
        }}
      >
        <h1 style={{ marginBottom: 10, color: "#202124" }}>Tinitiate</h1>
        <p style={{ color: "#5f6368", marginBottom: 30 }}>
          Sign in to continue
        </p>

        <button
          onClick={() => signIn("google")}
          style={{
            display: "flex",
            alignItems: "center",
            gap: 10,
            padding: "10px 20px",
            borderRadius: 6,
            border: "1px solid #dadce0",
            background: "#fff",
            cursor: "pointer",
            fontWeight: 500,
          }}
        >
          <img src="/google.png" width={20} />
          Sign in with Google
        </button>
      </div>
    </div>
  );
}
